.card {
  display: flex;
  flex-direction: column;
  background-color: #95dada;
  border: 1px solid gray;
  border-radius: 5px;
  padding: 15px;
  cursor: pointer;
  -moz-osx-font-smoothing: grayscale;
  backface-visibility: hidden;
  transform: translateZ(0);
  transition: transform .25s ease-out;
}

.card:hover {
  transform: scale(1.05);
}

.card__image {
  display: block;
  width: auto;
  max-width: 80%;
  height: auto;
  margin: 0 auto;
  object-fit: cover;
}
